.navBar {
    background: cornsilk;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title {
    margin: 0px;
}

.navBar ul {
    margin: 0px;
    display: flex;
    justify-content: space-evenly;
    align-self: center;
    gap: 10px;
}

.navBar li {
    font-size: larger;
    background: khaki;
    padding: 10px;
}